<template>
	<div>
		<el-dialog modal-class="edit_form_modal" class="edit_form" v-model="formVisible" :title="formTitle" width="80%" destroy-on-close :fullscreen='false'>
			<el-form class="formModel_form" ref="formRef" :model="form" :rules="rules">
				<el-row>
					<el-col :span="12">
						<el-form-item label="" prop="productname">
							<el-input class="list_inp" v-model="form.productname" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.productname?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="productcategory">
							<el-select
								class="list_sel"
								:disabled="!isAdd||disabledForm.productcategory?true:false"
								v-model="form.productcategory" 
								placeholder=""
								>
								<el-option v-for="(item,index) in productcategoryLists" :label="item"
									:value="item"
									>
								</el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item prop="image"
									  label=""
						>
							<uploads
								:disabled="!isAdd||disabledForm.image?true:false"
								action="file/upload"

								tip=""
								:limit="3"
								style="width: 100%;text-align: left;"
								:fileUrls="form.image?form.image:''" 
								@change="imageUploadSuccess">
							</uploads>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="" prop="specification">
							<el-input class="list_inp" v-model="form.specification" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.specification?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="placeofproduction">
							<el-input class="list_inp" v-model="form.placeofproduction" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.placeofproduction?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="account">
							<el-input class="list_inp" v-model="form.account" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.account?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="name">
							<el-input class="list_inp" v-model="form.name" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.name?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="price">
							<el-input class="list_inp" v-model.number="form.price" placeholder=""
								 type="number" 								:readonly="!isAdd||disabledForm.price?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="onelimittimes">
							<el-input class="list_inp" v-model.number="form.onelimittimes" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.onelimittimes?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="alllimittimes">
							<el-input class="list_inp" v-model.number="form.alllimittimes" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.alllimittimes?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="productdetails">
							<editor :value="form.productdetails" placeholder="" :readonly="!isAdd||disabledForm.productdetails?true:false"
								class="list_editor" @change="(e)=>editorChange(e,'productdetails')"></editor>
						</el-form-item>
					</el-col>
				</el-row>
			</el-form>
			<template #footer v-if="isAdd||type=='logistics'||type=='reply'">
				<span class="formModel_btn_box">
					<el-button class="cancel_btn" @click="closeClick"></el-button>
					<el-button class="confirm_btn" type="primary" @click="save"
						>
						
					</el-button>
				</span>
			</template>
		</el-dialog>
	</div>
</template>
<script setup>
	import {
		reactive,
		ref,
		getCurrentInstance,
		nextTick,
		computed,
		defineEmits
	} from 'vue'
    import {
        useStore
    } from 'vuex';
    const store = useStore()
    const user = computed(()=>store.getters['user/session'])
	const context = getCurrentInstance()?.appContext.config.globalProperties;	
	const emit = defineEmits(['formModelChange'])
	//
	const tableName = 'agriculturalproductinformation'
	const formName = ''
	//
	//form
	const form = ref({})
	const disabledForm = ref({
		productname : false,
		productcategory : false,
		image : false,
		specification : false,
		placeofproduction : false,
		productdetails : false,
		account : false,
		name : false,
		storeupNumber : false,
		discussNumber : false,
		price : false,
		onelimittimes : false,
		alllimittimes : false,
	})
	const formVisible = ref(false)
	const isAdd = ref(false)
	const formTitle = ref('')
	//
	//
	const validateIntNumber = (rule, value, callback) => {
		if (!value) {
			callback();
		} else if (!context?.$toolUtil.isIntNumer(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateNumber = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isNumber(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateMobile = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isMobile(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validatePhone = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isPhone(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateEmail = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isEmail(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateIdCard = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.checkIdCard(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateUrl = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isURL(value)) {
			callback(new Error("URL"));
		} else {
			callback();
		}
	}
	const rules = ref({
		productname: [
		],
		productcategory: [
		],
		image: [
		],
		specification: [
		],
		placeofproduction: [
		],
		productdetails: [
		],
		account: [
		],
		name: [
		],
		storeupNumber: [
			{ validator: validateIntNumber, trigger: 'blur' },
		],
		discussNumber: [
			{ validator: validateIntNumber, trigger: 'blur' },
		],
		price: [
			{ validator: validateNumber, trigger: 'blur' },
		],
		onelimittimes: [
			{ validator: validateIntNumber, trigger: 'blur' },
		],
		alllimittimes: [
			{ validator: validateIntNumber, trigger: 'blur' },
		],
	})
	//
	
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	//
	const productcategoryLists = ref([])
	//
	const imageUploadSuccess=(e)=>{
		form.value.image = e
	}
	//methods

	//
	const getUUID =()=> {
      return new Date().getTime();
    }
	//
	const resetForm = () => {
		form.value = {
			productname: '',
			productcategory: '',
			image: '',
			specification: '',
			placeofproduction: '',
			productdetails: '',
			account: '',
			name: '',
		}
	}
	//info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			res.data.data.productdetails = res.data.data.productdetails?(res.data.data.productdetails.replace(reg,'../../../project/file')):'';
			form.value = res.data.data
			formVisible.value = true
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//
	const init=(formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null)=>{
		resetForm()
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
			formTitle.value = '' + formName
			formVisible.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			formTitle.value = '' + formName
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			formTitle.value = '' + formName
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			formTitle.value = formNames
			// getInfo()
			for(let x in row){
				if(x=='productname'){
					form.value.productname = row[x];
					disabledForm.value.productname = true;
					continue;
				}
				if(x=='productcategory'){
					form.value.productcategory = row[x];
					disabledForm.value.productcategory = true;
					continue;
				}
				if(x=='image'){
					form.value.image = row[x];
					disabledForm.value.image = true;
					continue;
				}
				if(x=='specification'){
					form.value.specification = row[x];
					disabledForm.value.specification = true;
					continue;
				}
				if(x=='placeofproduction'){
					form.value.placeofproduction = row[x];
					disabledForm.value.placeofproduction = true;
					continue;
				}
				if(x=='productdetails'){
					form.value.productdetails = row[x];
					disabledForm.value.productdetails = true;
					continue;
				}
				if(x=='account'){
					form.value.account = row[x];
					disabledForm.value.account = true;
					continue;
				}
				if(x=='name'){
					form.value.name = row[x];
					disabledForm.value.name = true;
					continue;
				}
				if(x=='storeupNumber'){
					form.value.storeupNumber = row[x];
					disabledForm.value.storeupNumber = true;
					continue;
				}
				if(x=='discussNumber'){
					form.value.discussNumber = row[x];
					disabledForm.value.discussNumber = true;
					continue;
				}
				if(x=='price'){
					form.value.price = row[x];
					disabledForm.value.price = true;
					continue;
				}
				if(x=='onelimittimes'){
					form.value.onelimittimes = row[x];
					disabledForm.value.onelimittimes = true;
					continue;
				}
				if(x=='alllimittimes'){
					form.value.alllimittimes = row[x];
					disabledForm.value.alllimittimes = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
			formVisible.value = true
		}

		context?.$http({
			url: `${context?.$toolUtil.storageGet('sessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
			if(json.hasOwnProperty('account')&& context?.$toolUtil.storageGet("role")!=""){
				form.value.account = json.account
				disabledForm.value.account = true;
			}
			if(json.hasOwnProperty('name')&& context?.$toolUtil.storageGet("role")!=""){
				form.value.name = json.name
				disabledForm.value.name = true;
			}
		})
		context?.$http({
			url: `option/productcategory/productcategory`,
			method: 'get'
		}).then(res=>{
			productcategoryLists.value = res.data.data
		})
	}
	//
	//
	defineExpose({
		init
	})
	//
	const closeClick = () => {
		formVisible.value = false
	}
	//
	const editorChange = (e,name) =>{
		form.value[name] = e
	}
	//
	const save= async ()=>{
		if(form.value.image!=null) {
			form.value.image = form.value.image.replace(new RegExp(context?.$config.url,"g"),"");
		}
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//
					changeCrossData(objcross)
				}else{
					crossUserId = user.value.id
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(async res=>{
								emit('formModelChange')
								context?.$toolUtil.message(``,'success')
                                formVisible.value = false
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(async (res)=>{
						emit('formModelChange')
						context?.$toolUtil.message(``,'success')
                        formVisible.value = false
					})
				}
			}
		})
	}
	//
	const changeCrossData = async (row)=>{
		await context?.$http({
			url: `${crossTable.value}/update`,
			method: 'post',
			data: row
		}).then(res=>{})
	}
</script>
<style lang="scss" scoped>
	// 
	.formModel_form{
		// form item
		:deep(.el-form-item) {
			//label
			.el-form-item__label {
			}
			// 
			.el-form-item__content {
				// 
				.list_inp {
				}
				// 
				.list_sel {
					//
					.select-trigger{
						height: 100%;
						.el-input{
							height: 100%;
						}
					}
				}
				// 
				.list_editor {
				}
				//
				.el-upload-list  {
					//
					.el-upload__tip {
					}
					//
					.el-upload--picture-card {
						//
						.el-icon{
						}
					}
					.el-upload-list__item {
					}
				}
			}
		}
	}
	// 
	.formModel_btn_box {
		.cancel_btn {
		}
		.cancel_btn:hover {
		}
		
		.confirm_btn {
		}
		.confirm_btn:hover {
		}
	}
</style>
