<template>
	<div>
		<div class="register_view">
			<div class="outTitle_view">
				<el-image class="outTitle_img" src="http://clfile.zggen.cn/20240921/f930544101f94cbba9f175d2472cba0d.png" fit="cover"></el-image>
				<div class="outTilte">{{projectName}}</div>
			</div>
			<el-form :model="registerForm" class="register_form">
				<div class="list_item">
					<div class="list_label"></div>
					<el-input class="list_inp"
						 v-model="registerForm.account" 
						 placeholder=""
						 type="text"
						/>
				</div>
				<div class="list_item">
					<div class="list_label"></div>
					<el-input class="list_inp"
						 v-model="registerForm.password" 
						 placeholder=""
						 type="password"
						 />
				</div>
				<div class="list_item">
					<div class="list_label"></div>
					<el-input class="list_inp" v-model="registerForm.password2" type="password" placeholder="" />
				</div>
				<div class="list_item">
					<div class="list_label"></div>
					<el-input class="list_inp"
						 v-model="registerForm.name" 
						 placeholder=""
						 type="text"
						/>
				</div>
				<div class="list_item">
					<div class="list_label"></div>
					<el-input class="list_inp"
						 v-model="registerForm.contactPhone" 
						 placeholder=""
						 type="text"
						/>
				</div>
				<div class="list_item">
					<div class="list_label"></div>
					<el-input class="list_inp"
						 v-model="registerForm.address" 
						 placeholder=""
						 type="text"
						/>
				</div>
				<div class="list_btn">
					<el-button class="register" type="success" @click="handleRegister"></el-button>
					<div class="r-login" @click="close"></div>
				</div>
			</el-form>
		</div>
	</div>
</template>
<script setup>
	import {
		ref,
		getCurrentInstance,
		nextTick,
		onMounted,
	} from 'vue';
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const projectName = context?.$project.projectName
	//
	import { useRoute } from 'vue-router';
	const route = useRoute()
	const tableName = ref('supplier')
	
	//
	const getUUID=()=> {
		return new Date().getTime();
	}
	const registerForm = ref({
	})
	const init=()=>{
	}
	// 
	//
	const handleRegister = () => {
		let url = tableName.value +"/register";
		if((!registerForm.value.account)){
			context?.$toolUtil.message(``,'error')
			return false
		}
		if((!registerForm.value.password)){
			context?.$toolUtil.message(``,'error')
			return false
		}
		if(registerForm.value.password!=registerForm.value.password2){
			context?.$toolUtil.message('','error')
			return false
		}
		if((!registerForm.value.name)){
			context?.$toolUtil.message(``,'error')
			return false
		}
		if(registerForm.value.contactPhone&&(!context?.$toolUtil.isMobile(registerForm.value.contactPhone))){
			context?.$toolUtil.message(``,'error')
			return false
		}
		context?.$http({
			url:url,
			method:'post',
			data:registerForm.value
		}).then(res=>{
			context?.$toolUtil.message('','success', obj=>{
				context?.$router.push({
					path: "/login"
				});
			})
		})
	}
	//
	const close = () => {
		context?.$router.push({
			path: "/login"
		});
	}
	init()
	onMounted(()=>{

	})
</script>
