<template>
	<div>
		<div class="center_view">
			<el-tabs
			    v-model="orderStatus"
			    type="card"
			    class="demo-tabs"
				@tab-change="orderTabClick"
				>
				<el-tab-pane label="" :name="''"></el-tab-pane>
				<el-tab-pane label="" name="" v-if="btnAuth(`orders/`,'')"></el-tab-pane>
				<el-tab-pane label="" name="" v-if="btnAuth(`orders/`,'')"></el-tab-pane>
				<el-tab-pane label="" name="" v-if="btnAuth(`orders/`,'')"></el-tab-pane>
				<el-tab-pane label="" name="" v-if="btnAuth(`orders/`,'')"></el-tab-pane>
				<el-tab-pane label="" name="" v-if="btnAuth(`orders/`,'')"></el-tab-pane>
				<el-tab-pane label="" name="" v-if="btnAuth(`orders/`,'')"></el-tab-pane>
				<el-tab-pane label="" name="" v-if="btnAuth(`orders/`,'')"></el-tab-pane>
			</el-tabs>
			<div class="list_search_view">
				<el-form :model="searchQuery" class="search_form" >
					<div class="search_view">
						<div class="search_label">
							
						</div>
						<div class="search_box">
							<el-input class="search_inp" v-model="searchQuery.orderid" placeholder=""
								clearable>
							</el-input>
						</div>
					</div>
					<div class="search_view">
						<div class="search_label">
							
						</div>
						<div class="search_box">
							<el-select
								class="search_sel"
								clearable
								v-model="searchQuery.sfsh"
								placeholder=""
								>
								<el-option v-for="item in approvalLists" :label="item" :value="item"></el-option>
							</el-select>
						</div>
					</div>
					<div class="search_btn_view">
						<el-button class="search_btn" type="primary" @click="searchClick()" size="small"></el-button>
					</div>
				</el-form>
				<div class="btn_view">
					<el-button class="add_btn" type="success" @click="addClick" v-if="btnAuth('orders','')">
						
					</el-button>
					<el-button class="del_btn" type="danger" :disabled="selRows.length?false:true" @click="delClick(null)"  v-if="btnAuth('orders'+(orderStatus?'/'+orderStatus:''),'')">
						
					</el-button>
					<el-button class="other_btn" type="default" :disabled="selRows.length?false:true" @click="exportClick" v-if="btnAuth('orders'+(orderStatus?'/'+orderStatus:''),'')">
						
					</el-button>
				</div>
			</div>
			<br>
			<el-table
				v-loading="listLoading"
				border
				:stripe='false'
				@selection-change="handleSelectionChange"
				ref="table"
				v-if="btnAuth('orders'+(orderStatus?'/'+orderStatus:''),'')"
				:data="list"
				@row-click="listChange">
				<el-table-column :resizable='true' align="left" header-align="left" type="selection" width="55" />
				<el-table-column label="" width="70" :resizable='true' align="left" header-align="left">
					<template #default="scope">{{ (listQuery.page-1)*listQuery.limit+scope.$index + 1}}</template>
				</el-table-column>
				<el-table-column min-width="140"
					 :resizable='true'
					 :sortable='true'
					 align="left"
					 header-align="left"
					 prop="orderid"
					label="">
					<template #default="scope">
						{{scope.row.orderid}}
					</template>
				</el-table-column>
				<el-table-column min-width="140"
					 :resizable='true'
					 :sortable='true'
					 align="left"
					 header-align="left"
					 prop="goodname"
					label="">
					<template #default="scope">
						{{scope.row.goodname}}
					</template>
				</el-table-column>
				<el-table-column label="" min-width="140" width="120" :resizable='true' :sortable='true' align="left" header-align="left">
					<template #default="scope">
						<div v-if="scope.row.picture">
							<el-image v-if="scope.row.picture.substring(0,4)=='http'" preview-teleported
								:preview-src-list="[scope.row.picture.split(',')[0]]"
								:src="scope.row.picture.split(',')[0]" style="width:100px;height:100px"></el-image>
							<el-image v-else preview-teleported
								:preview-src-list="[$config.url+scope.row.picture.split(',')[0]]"
								:src="$config.url+scope.row.picture.split(',')[0]" style="width:100px;height:100px">
							</el-image>
						</div>
						<div v-else></div>
					</template>
				</el-table-column>
				<el-table-column min-width="140"
					 :resizable='true'
					 :sortable='true'
					 align="left"
					 header-align="left"
					 prop="buynumber"
					label="">
					<template #default="scope">
						{{scope.row.buynumber}}
					</template>
				</el-table-column>
				<el-table-column min-width="140"
					 :resizable='true'
					 :sortable='true'
					 align="left"
					 header-align="left"
					 prop="price"
					label="">
					<template #default="scope">
						{{scope.row.price}}
					</template>
				</el-table-column>
				<el-table-column min-width="140"
					 :resizable='true'
					 :sortable='true'
					 align="left"
					 header-align="left"
					 prop="discountprice"
					label="">
					<template #default="scope">
						{{scope.row.discountprice}}
					</template>
				</el-table-column>
				<el-table-column min-width="140"
					 :resizable='true'
					 :sortable='true'
					 align="left"
					 header-align="left"
					 prop="total"
					label="">
					<template #default="scope">
						{{scope.row.total}}
					</template>
				</el-table-column>
				<el-table-column min-width="140"
					 :resizable='true'
					 :sortable='true'
					 align="left"
					 header-align="left"
					 prop="discounttotal"
					label="">
					<template #default="scope">
						{{scope.row.discounttotal}}
					</template>
				</el-table-column>
				<el-table-column min-width="140"
					 :resizable='true'
					 :sortable='true'
					 align="left"
					 header-align="left"
					 prop="type"
					:formatter="orderStatusFormatter"
					label="">
				</el-table-column>
				<el-table-column min-width="140"
					 :resizable='true'
					 :sortable='true'
					 align="left"
					 header-align="left"
					 prop="status"
					label="">
					<template #default="scope">
						{{scope.row.status}}
					</template>
				</el-table-column>
				<el-table-column min-width="140"
					 :resizable='true'
					 :sortable='true'
					 align="left"
					 header-align="left"
					 prop="address"
					label="">
					<template #default="scope">
						{{scope.row.address}}
					</template>
				</el-table-column>
				<el-table-column min-width="140"
					 :resizable='true'
					 :sortable='true'
					 align="left"
					 header-align="left"
					 prop="tel"
					label="">
					<template #default="scope">
						{{scope.row.tel}}
					</template>
				</el-table-column>
				<el-table-column min-width="140"
					 :resizable='true'
					 :sortable='true'
					 align="left"
					 header-align="left"
					 prop="consignee"
					label="">
					<template #default="scope">
						{{scope.row.consignee}}
					</template>
				</el-table-column>
				<el-table-column min-width="140"
					 :resizable='true'
					 :sortable='true'
					 align="left"
					 header-align="left"
					 prop="remark"
					label="">
					<template #default="scope">
						{{scope.row.remark}}
					</template>
				</el-table-column>
				<el-table-column min-width="140"
					 :resizable='true'
					 :sortable='true'
					 align="left"
					 header-align="left"
					 prop="addtime"
					label="">
					<template #default="scope">
						{{scope.row.addtime}}
					</template>
				</el-table-column>
				<el-table-column min-width="140"
					 :resizable='true'
					 :sortable='true'
					 align="left"
					 header-align="left"
					 prop="account"
					label="">
					<template #default="scope">
						{{scope.row.account}}
					</template>
				</el-table-column>
                <template v-if="!orderStatus||orderStatus==''||orderStatus==''">
                    <el-table-column label="" min-width="140" :resizable='true' :sortable='true' align="left" header-align="left">
                        <template #default="scope">
                            {{scope.row.shhf}}
                        </template>
                    </el-table-column>
                    <el-table-column label="" min-width="140" :resizable='true' :sortable='true' align="left" header-align="left">
                        <template #default="scope">
                            <el-tag type="success" v-if="scope.row.sfsh==''"></el-tag>
                            <el-tag type="danger" v-else-if="scope.row.sfsh==''"></el-tag>
                            <el-tag type="warning" v-else-if="scope.row.sfsh==''"></el-tag>
                        </template>
                    </el-table-column>
                    <el-table-column label="" v-if="btnAuth('orders'+(orderStatus?'/'+orderStatus:''),'')" :resizable='true' :sortable='true' align="left" header-align="left">
                        <template #default="scope">
                            <el-button v-if="scope.row.sfsh==''" link @click="approvalClick(scope.row)"></el-button>
                        </template>
                    </el-table-column>
                </template>
				<el-table-column prop="addtime" label="" min-width="140" :resizable='true' :sortable='true' align="left" header-align="left">
					<template #default="scope">
						{{scope.row.addtime}}
					</template>
				</el-table-column>
				<el-table-column label="" width="300" :resizable='true' :sortable='true' align="left" header-align="left">
					<template #default="scope">
						<el-button class="view_btn" type="info" v-if=" btnAuth('orders'+(orderStatus?'/'+orderStatus:''),'')" @click="infoClick(scope.row.id)">
							
						</el-button>
						<el-button class="edit_btn" type="primary" @click="editClick(scope.row.id)" v-if=" btnAuth('orders'+(orderStatus?'/'+orderStatus:''),'')">
													</el-button>
						<el-button class="del_btn" type="danger" @click="delClick(scope.row.id)"  v-if="btnAuth('orders'+(orderStatus?'/'+orderStatus:''),'')">
													</el-button>
						<el-button class="operate_btn" v-if="btnAuth('orders'+(scope.row.status?'/'+scope.row.status:''),'')" type="warning" @click="logisticsClick(scope.row)">
							
						</el-button>
						<el-button class="operate_btn" v-if="btnAuth('orders'+(scope.row.status?'/'+scope.row.status:''),'')" type="warning" @click="sendGoodClick(scope.row)">
							
						</el-button>
						<el-button class="operate_btn" v-if="btnAuth('orders'+(scope.row.status?'/'+scope.row.status:''),'')" type="warning" @click="confirmGoodClick(scope.row)">
							
						</el-button>
					</template>
				</el-table-column>
			</el-table>
			<el-pagination
				background
				:layout="layouts.join(',')"
				:total="total"
				:page-size="listQuery.limit"
                v-model:current-page="listQuery.page"
				prev-text=""
				next-text=""
				:hide-on-single-page="false"
				:style='{}'
				:page-sizes="[10, 20, 30, 40, 50, 100]"
				@size-change="sizeChange"
				@current-change="currentChange"  />
		</div>
		<formModel ref="formRef" @formModelChange="formModelChange"></formModel>
		<Approval ref="approvalRef" :tableName="tableName" @shChange="shChange"></Approval>
	</div>
</template>
<script setup>
	import axios from 'axios'
	import {
		reactive,
		ref,
		getCurrentInstance,
		nextTick,
		onMounted,
		watch,
		computed,
	} from 'vue'
	import {
		useRoute,
		useRouter
	} from 'vue-router'
	import {
		ElMessageBox
	} from 'element-plus'
	import {
		useStore
	} from 'vuex';
	const store = useStore()
	const user = computed(()=>store.getters['user/session'])
	const avatar = ref(store.state.user.avatar)
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	import formModel from './formModel.vue'
	//

	const tableName = 'orders'
	const formName = ''
	const route = useRoute()
	watch(() => route.query,() => {
		init()
	})
	//
	onMounted(()=>{
	})
	//
	const list = ref(null)
	const table = ref(null)
	const listQuery = ref({
		page: 1,
		limit: 20,
		sort: 'id',
		order: 'desc'
	})
	const searchQuery = ref({})
	const selRows = ref([])
	const listLoading = ref(false)
	const listChange = (row) =>{
		nextTick(()=>{
			//table.value.clearSelection()
			table.value.toggleRowSelection(row)
		})
	}
	//
	const getList = () => {
		listLoading.value = true
		let params = JSON.parse(JSON.stringify(listQuery.value))
		params['sort'] = 'id'
		params['order'] = 'desc'
		if(orderStatus.value){
			params['status'] = orderStatus.value
		}
		if(searchQuery.value.orderid&&searchQuery.value.orderid!=''){
			params['orderid'] = '%' + searchQuery.value.orderid + '%'
		}
		if(searchQuery.value.sfsh && searchQuery.value.sfsh!=''){
			params['sfsh'] = searchQuery.value.sfsh
		}
		context.$http({
			url: `${tableName}/page`,
			method: 'get',
			params: params
		}).then(res => {
			listLoading.value = false
			list.value = res.data.data.list
			total.value = Number(res.data.data.total)
		})
	}
	//
	const delClick = (id) => {
		let ids = ref([])
		if (id) {
			ids.value = [id]
		} else {
			if (selRows.value.length) {
				for (let x in selRows.value) {
					ids.value.push(selRows.value[x].id)
				}
			} else {
				return false
			}
		}
		ElMessageBox.confirm(`${formName}`, '', {
			confirmButtonText: '',
			cancelButtonText: '',
			type: 'warning',
		}).then(() => {
			context.$http({
				url: `${tableName}/delete`,
				method: 'post',
				data: ids.value
			}).then(res => {
				context?.$toolUtil.message('', 'success',()=>{
					getList()
				})
			})
		})
	}
	//
	const handleSelectionChange = (e) => {
		selRows.value = e
	}
	//
	//
	const total = ref(0)
	const layouts = ref(["total","prev","pager","next","sizes","jumper"])
	const sizeChange = (size) => {
		listQuery.value.limit = size
		getList()
	}
	const currentChange = (page) => {
		listQuery.value.page = page
		getList()
	}
	//
	//
	const btnAuth = (e,a)=>{
		return context?.$toolUtil.isAuth(e,a)
	}
    const shChange = async (type,row)=>{
        if(type==''){
            let res = await context.$http.get(`${row.role}/info/${row.userid}`)
            let userinfo = res.data.data
            context.$http({
            	url: `${row.tablename}/info/${row.goodid}`,
            	method: 'get'
            }).then(res => {
            	let data = res.data.data
            	// 
            	if (data.alllimittimes) {
            		data.alllimittimes = parseInt(data.alllimittimes) + parseInt(row.buynumber)
            		// 
            		context.$http({
            			url: `${row.tablename}/update`,
            			method: 'post',
            			data: data
            		}).then(obj1 => {})
            	}
            	if (row.type == 2) {
            		// 
            		userinfo.jf = parseInt(userinfo.jf) + parseInt(row.total)
            		// 
            		context.$http({
            			url: `${row.role}/update`,
            			method: 'post',
            			data: userinfo
            		}).then(obj => {
            			row.status = ''
            			// 
            			context.$http({
            				url: 'orders/update',
            				method: 'post',
            				data: row
            			}).then(res1 => {
                            context.$message.success("")
            			})
            		})
            	} else {
            		// 
            		if (data.jf) {
            			userinfo.jf = parseInt(userinfo.jf) - parseInt(row.total)
            		}
            		// 
            		userinfo.money = (parseFloat(userinfo.money) + parseFloat(row.total)).toFixed(2)
            		// 
            		context.$http({
            			url: `${row.role}/update`,
            			method: 'post',
            			data: userinfo
            		}).then(obj => {
            			row.status = ''
            			// 
            			context.$http({
            				url: 'orders/update',
            				method: 'post',
            				data: row
            			}).then(res1 => {
                            context.$message.success("")
            			})
            		})
            	}
            })
        }
        searchClick()
    }
	//
	const searchClick = () => {
		listQuery.value.page = 1
		getList()
	}
	//
	const formRef = ref(null)
	const formModelChange=()=>{
		searchClick()
	}
	const addClick = ()=>{
		formRef.value.init()
	}
	const editClick = (id=null)=>{
		if(id){
			formRef.value.init(id,'edit')
			return
		}
		if(selRows.value.length){
			formRef.value.init(selRows.value[0].id,'edit')
		}
	}

	const infoClick = (id=null)=>{
		if(id){
			formRef.value.init(id,'info')
		}
		else if(selRows.value.length){
			formRef.value.init(selRows.value[0].id,'info')
		}
	}
	// 
	// 
	const preClick = (file) =>{
		if(!file){
			context?.$toolUtil.message('','error')
		}
		window.open(context?.$config.url + file)
	}
	// 
	const download = (file) => {
		if(!file){
			context?.$toolUtil.message('','error')
		}
		let arr = file.replace(new RegExp('file/', "g"), "")
		axios.get((location.href.split(context?.$config.name).length>1 ? location.href.split(context?.$config.name)[0] :'') + context?.$config.name + '/file/download?fileName=' + arr, {
			headers: {
				token: context?.$toolUtil.storageGet('Token')
			},
			responseType: "blob"
		}).then(({
			data
		}) => {
			const binaryData = [];
			binaryData.push(data);
			const objectUrl = window.URL.createObjectURL(new Blob(binaryData, {
				type: 'application/pdf;chartset=UTF-8'
			}))
			const a = document.createElement('a')
			a.href = objectUrl
			a.download = arr
			// a.click()
			// 
			a.dispatchEvent(new MouseEvent('click', {
				bubbles: true,
				cancelable: true,
				view: window
			}))
			window.URL.revokeObjectURL(data)
		})
	}
    import '@/assets/js/echarts-theme'
	//
	const exportClick = () => {
		import('@/utils/Export2Excel').then(excel => {
			const tHeader = [
				"",
				"",
				"id",
				"",
				"",
				"",
				"",
				"",
				"",
				"",
				"",
				"",
				"",
				"",
				"",
				"",
				"",
				"",
				"",
				"id",
				"",
				"",
				"",
			]
			const filterVal = [
				"orderid",
				"tablename",
				"goodid",
				"goodname",
				"picture",
				"buynumber",
				"price",
				"discountprice",
				"total",
				"discounttotal",
				"type",
				"status",
				"address",
				"tel",
				"consignee",
				"remark",
				"logistics",
				"role",
				"addtime",
				"userid",
				"sfsh",
				"shhf",
				"account",
			]
			excel.export_json_to_excel2(tHeader, selRows.value, filterVal, formName)
		})
	}
	//
	import Approval from '@/components/common/approval.vue'
	const approvalRef = ref(null)
	const approvalClick = (row) => {
		let params = {
			id:row.id,
			orderid: row.orderid,
			tablename: row.tablename,
			goodid: row.goodid,
			goodname: row.goodname,
			picture: row.picture,
			buynumber: row.buynumber,
			price: row.price,
			discountprice: row.discountprice,
			total: row.total,
			discounttotal: row.discounttotal,
			type: row.type,
			status: row.status,
			address: row.address,
			tel: row.tel,
			consignee: row.consignee,
			remark: row.remark,
			logistics: row.logistics,
			role: row.role,
			addtime: row.addtime,
			userid: row.userid,
			sfsh: row.sfsh,
			shhf: row.shhf,
			account: row.account,
		}
		nextTick(() => {
			approvalRef.value.approvalClick(params )
		})
	}
	const orderTabClick = () => {
		getList()
	}
	const orderStatus = ref('')
    if([''  ,'','','','',''].includes(route.query.menuJump)){
        orderStatus.value = route.query.menuJump
    }
	const orderStatusFormatter =(row, column)=> {
      var temp = ''
      switch (row.type-0) {
        case 1:
          temp = ''
          break
        case 2:
          temp = ''
          break
        case 3:
          temp = ''
          break
      }
      return temp
    }
	//
	const logisticsClick = (row)=>{
		formRef.value.init(row.id,'logistics','',row)
	}
	//
	const sendGoodClick=(row)=>{
		ElMessageBox.confirm(``, '', {
			confirmButtonText: '',
			cancelButtonText: '',
			type: 'warning',
		}).then(()=>{
			row.status = ''
			context.$http({
				url: 'orders/update',
				method: 'post',
				data: row
			}).then(res=>{
				context?.$toolUtil.message('','success',()=>{
					searchClick()
				})
			})
		})
	}
	//
	const confirmGoodClick=(row)=>{
		ElMessageBox.confirm(``, '', {
			confirmButtonText: '',
			cancelButtonText: '',
			type: 'warning',
		}).then(()=>{
			row.status = ''
			context.$http({
				url: 'orders/update',
				method: 'post',
				data: row
			}).then(res=>{
				context?.$toolUtil.message('','success',()=>{
					searchClick()
				})
			})
		})
	}
	//
	const approvalLists = ref([])
	//
	const init = () => {
